.popup {
    display: flex;
    margin: 0 auto;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    visibility: hidden;
    opacity: 0;
    transition: visibility 0.5s, opacity 0.5s linear;
    background: rgba(0, 0, 0, 0.5);
    z-index: 4;
}
.popup__close {
    background-image: url(/react-mesto-auth/static/media/CloseIcon.cb0f2bbf.svg);
    background-size: cover;
    background-color: rgba(0, 0, 0, 0);
    border: none;
    width: 32px;
    height: 32px;
    position: absolute;
    top: -40px;
    right: -40px;
    cursor: pointer;
    margin: 0;
    padding: 0;
}

.popup__close:hover {
    opacity: 0.6;
}
@media screen and (max-width: 787px) {
    .popup__close {
        top: -40px;
        right: 0;
        width: 20px;
